import React , {useRef , useState , useEffect , useReducer , useContext} from 'react'
import { faCheck, faTimes, faInfoCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./css/verification.css";
import gif from "./img/icon-8.gif";
import Ok_icon from "./img/icon-8.png";
import axios from "../../api/axios"
import loading from "./img/loading.png";
import { filed } from '../../App';
import { useNavigate } from 'react-router-dom';
import Login from '../login/Login';



const Registration = props => {
    let Navigate  = useNavigate()
    const {i ,z} = useContext(filed);

let [int , setInt] = useState({
    int_1 : "5" ,
    int_2 : "5" ,
    int_3 : "3" ,
    int_4 : "8" ,
    int_5 : "9" ,
    int_6 : "2" ,

})
let [keypress , setKeyPress] = useState();
let int_1 = useRef();
let int_2 = useRef();
let int_3 = useRef();
let int_4 = useRef();
let int_5 = useRef();
let int_6 = useRef();

let [Loading , setLoainding] = useState();
let sign_btn = "sign-btn";
let [ resendCount , setResendCount ] = useState(30);
let [message , setMessage] = useState(null)
let [error , setError] = useState({
    message : "" , 
    display : "none"
})

const handle_int1 = async (e)=>{

   

    let regex = /^[0-9]*$/;
    if(regex.test(e.target.value)){
        setInt(prev=> ({...prev , [e.target.name] : e.target.value.split("").reverse().join("").substring(0,1)}));
        if (keypress != 8){
        if(e.target.name === "int_6"){
            return null
        } else {
            let key = evalFunc(`int_${Number(e.target.name.split("_")[1]) + 1}`)
            key.current.focus(); } 
        }
    }

       
   
    }



    useEffect(()=>{
        let arr = []
       
        for(let i = 1;i <= 6;i++){
            arr.push(int[`int_${i}`])
           
        }

        if(arr.length == 6 ){
            handle_submit()
        }
        setError(prev=> ({display : "none"}))

    } , [int])

    useEffect(()=>{
        int_1.current.focus()
         int_1.current.setSelectionRange(1,1);

    } , [])



    const handle_key = async (e)=>{
    setKeyPress(e.keyCode);

if(e.keyCode == 8){
   let x =   Number(e.target.name.split("_")[1]) - 1

   let y =   Number(e.target.name.split("_")[1]) 
   if(x == 0){
    return null
   } else {
  let key = evalFunc(`int_${x}`);
  let key2 = evalFunc(`int_${y}`);
  setInt((prev)=> ({...prev , [key2.current.name] : ""}))
  if(e.target.value.length == 0){;
  key.current.focus()
  }
}
};



switch (e.target.name) {

    case "int_1" :
        switch (e.keyCode) {
            case 39:
                int_2.current.focus()
           int_2.current.setSelectionRange(0,0);

                break;
           case 37 : 
           int_6.current.focus()
           setTimeout(()=>{
               int_6.current.setSelectionRange(1,1);
           } , 1)
            break;
        }
        break;
        case "int_2" : 
        switch (e.keyCode) {
            case 39:
                int_3.current.focus()
                break;
        
           case 37 : 
           
           int_1.current.focus()
           setTimeout(()=>{
            int_1.current.setSelectionRange(1,1);
        } , 1)


         
            break;
        }
        break;
        case "int_3" : 
        switch (e.keyCode) {
            case 39:
                int_4.current.focus()
                break;
        
           case 37 : 
            int_2.current.focus()
            setTimeout(()=>{
                int_2.current.setSelectionRange(1,1);
            } , 1)
            break;
        }
        break;
        case "int_4" :
            switch (e.keyCode) {
                case 39:
                    int_5.current.focus()
                    break;
            
               case 37 : 
                int_3.current.focus()
                setTimeout(()=>{
                    int_3.current.setSelectionRange(1,1);
                } , 1)


                break;
            }
            break;
        case "int_5" :
            switch (e.keyCode) {
                case 39:
                    int_6.current.focus()
                    break;
            
               case 37 : 
                int_4.current.focus()
                setTimeout(()=>{
                    int_4.current.setSelectionRange(1,1);
                } , 1)


                break;
            }
            break;
            case "int_6" :
                switch (e.keyCode) {
                    case 39:
                        int_1.current.focus()
                        break;
                   case 37 : 
                    int_5.current.focus()
                    setTimeout(()=>{
                        int_5.current.setSelectionRange(1,1);
                    } , 1)
                    break;
                }
                
                
        
}
}


let handle_submit = async (e)=>{
    try {
        
        let arr = []
       
            for(let i = 1;i <= 6;i++){
                arr.push(int[`int_${i}`])
               
            }
        if (arr.join("").length == 6){
            let obj = {};
            let x = document.cookie
            if(x){
                let token = document.cookie.split(";").forEach((data)=>{
                    let key = data.split("=")[0].trim(`"`)
                    let token = data.split("=")[1]
                    obj[key] = token
                });
                  let resp  = await axios.post(`/verify/${obj.verification}` , {
                  
                    code : arr.join("")
                
                  })
                  setError(prev=> ({display : "block" , color : true , message : resp.data}))
                  document.cookie = `verification=; Expires=Jan 5 1970 22:20:00; path=/; SameSite=Lax`
                  setTimeout(()=>{ 
                  Navigate("/login")
                  }  , 1000)
                 
             
            } else {
                Navigate("/login")
                console.log("no token");
            }
        }
    } catch (error) {
        console.log(error.response.data);
        setError(prev=> ({display : "block" , message : error.response.data.message}))
        console.log(error.response);
    }
}




function evalFunc(obj){
    return eval(`(${obj})`)
}

useEffect(()=>{
let interval
    if(resendCount > 0){
         interval = setInterval(()=>{    
            setResendCount((prev)=> prev - 1)      
        } , 1000)     
    } else{
        setResendCount("")
        clearInterval(interval)
    }
return ()=>{
    clearInterval(interval)
}
} , [resendCount]);


let Verification_route = async ()=>{
    try {
     
        if(!i.Verification_route){
            let obj = {};
            let x = document.cookie
            if(x){
                
                let token = document.cookie.split(";").forEach((data)=>{
                    let key = data.split("=")[0].trim(`"`)
                    let token = data.split("=")[1]
                    obj[key] = token
                });
                 let resp = await axios.get(`/verification/${obj.verification}`);
                  setMessage(resp.data.message)

            } else {
                Navigate("/login")
                console.log("no token");
            }
        
        }
    } catch (error) {
        Navigate("/login")
        console.log("no token");
    }

}
useEffect(()=>{
Verification_route()
} , []);



return (
    <>
    <section className='verification'>
   <div className='registration_form'>
<div>{i.Verification.length ? i.Verification : message}</div>
<form action="">
    <input type="text" name="int_1" ref={int_1} onKeyDown={(e)=> handle_key(e)} value={int.int_1} onChange={(e)=>{  handle_int1(e) }} />
    <input type="text" name="int_2" ref={int_2} onKeyDown={(e)=> handle_key(e)} value={int.int_2} onChange={(e)=>{ handle_int1(e) }} />
    <input type="text" name="int_3" ref={int_3} onKeyDown={(e)=> handle_key(e)} value={int.int_3} onChange={(e)=>{  handle_int1(e) }} />
    <input type="text" name="int_4" ref={int_4} onKeyDown={(e)=> handle_key(e)} value={int.int_4} onChange={(e)=>{handle_int1(e) }} />
    <input type="text" name="int_5" ref={int_5} onKeyDown={(e)=> handle_key(e)} value={int.int_5} onChange={(e)=>{ handle_int1(e) }} />
   <input type="text" name="int_6" ref={int_6} onKeyDown={(e)=> handle_key(e)} value={int.int_6}  onChange={(e)=>{   handle_int1(e) }} />
</form>
  <div style={{display : error.display , color : error.color ? ("green") : ("red")}} className='error'>{error.message}</div>
   <footer className=''>
            <button >resend the code {resendCount}</button>
            <button type='submit' className={`${sign_btn} sign-btn ${Loading ? ("loading") : ("")}`} onClick={(e)=> handle_submit(e)} >{Loading ? (<img src={loading} alt="" />) : ("Next")}</button>
        </footer>
  
   </div>
    </section>  
    </>
)
}




export default Registration