import React , {useRef , useState , useEffect , useReducer , useContext} from 'react'
import { faCheck, faTimes, faInfoCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./css/registration.css";
import gif from "./img/icon-8.gif";
import Ok_icon from "./img/icon-8.png";
import axios from "../../api/axios"
import loading from "./img/loading.png";
import {filed} from "../../App";
import { useNavigate, useNavigation , Route} from "react-router-dom";


 

const Registration = () => {
    let navigate = useNavigate();

    let {i , z } = useContext(filed);

     console.log(i);



    let useref = useRef(null);

    useEffect(()=>{
 useref.current.focus()
    } , [])
    let [user , setuer] = useState({
        username : "" , 
        sirname : "" , 
        email : "" , 
        password : "" , 
        confirm_password : "" , 
        birth : ""
    });

    //css style states
   let [classNames , setClassNames] = useState({ 
    username : "" , 
    sirname : "" , 
    email : "" , 
    password : "" , 
    confirm_password : "" , 
    birth : ""
    
   })
   let [okay_img , set_okay_img] = useState({
    username : "" , 
    sirname : "", 
    email : "" , 
    password : "" ,
    confirm_password : "" , 
    birth : ""
   })


   let [pwd_ds , setPwd_ds] = useState({
    ok_icon : "" , 
    ds : ""
   })
   let [confirm_pwd_ds ,set_confirm_pwd_ds ] = useState(null)
   let [ sign_btn , setSignBtn] = useState("disabled")
   let [error , setError] = useState({
    class : "none" , 
    data : ""
   })
   let [Loading , setLoainding] = useState(false);

const handle_username = async (e)=>{
    let username_regex =  /^[A-z][A-z-_]{3,23}$/;
    console.log(username_regex.test(e.target.value));
    if(username_regex.test(e.target.value.trim())){        
            set_okay_img((prev)=>{return {...prev , username : Ok_icon }})
        
    } else {  
        set_okay_img((prev)=>{return {...prev , username : null}});
    }
}

const handle_sirname = async (e)=>{
    let username_regex =  /\b[a-zA-Z]{4,50}\b/m;
    if(username_regex.test(e.target.value.trim())){  

            set_okay_img((prev)=>{return {...prev , sirname : Ok_icon }})
        
    } else {  
        set_okay_img((prev)=>{return {...prev , sirname : null}});
    }
}
const handle_email = async (e)=>{
    let email_regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    console.log(email_regex.test(e.target.value));
    if(email_regex.test(e.target.value)){  

            set_okay_img((prev)=>{return {...prev , email : Ok_icon }})
        
    } else {  
        set_okay_img((prev)=>{return {...prev , email : null}});
    }
}
const handle_password = async (e)=>{
    let password_regex = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/;
    console.log(password_regex.test(e.target.value));
    console.log(user.confirm_password);
    if(user.confirm_password !== e.target.value) {
        set_okay_img((prev)=>{return {...prev , confirm_password : null}});
     
    }
    if(password_regex.test(e.target.value)){  
            set_okay_img((prev)=>{return {...prev , password : Ok_icon }});
            if(user.confirm_password === e.target.value) {
                set_okay_img((prev)=>{return {...prev , confirm_password : Ok_icon }})

            
            }

        
    } else {  
        set_okay_img((prev)=>{return {...prev , password : null}});
    set_okay_img((prev)=>{return {...prev , confirm_password : null}});

    }
}


const handle_confirmPassword = async (e)=>{
   if(e.target.value === user.password && okay_img.password){
     
    set_okay_img((prev)=>{return {...prev , confirm_password : Ok_icon }})
        
} else {  
    set_okay_img((prev)=>{return {...prev , confirm_password : null}});
}


}

const handle_birth = async (r)=>{
        set_okay_img((prev)=>{return {...prev , birth: Ok_icon }})
    

}


useEffect(()=>{
    setError((prev)=>{ return {class : "none" , data : ""}})

if(okay_img.username && okay_img.sirname && okay_img.email && okay_img.password && okay_img.confirm_password && okay_img.birth){
    setSignBtn("disabled")
}

return setSignBtn("")

} , [user.username , user.sirname , user.email , user.password , user.confirm_password , user.birth])

const handle_submit = async ()=>{

    setError((prev)=>{ return {class : "none" , data : ""}});
    console.log(window.Navigator.onLine);
    if(!window.navigator.onLine){
       return setError((prev)=>{ return {class : "block" , data : "no network connection"}});
    }
      
    // if(okay_img.username && okay_img.sirname && okay_img.email && okay_img.password && okay_img.confirm_password && okay_img.birth && !Loading){
        if(!window.navigator.cookieEnabled){
 
            return  setError((prev)=>{ return {class : "block" , data : "cookies must be enabled"}});

        }
       try {
        setLoainding(true)
        let resp = await axios.post("/register" , {...user} , {
            headers : {
                Accept : "application/json" , 
                "Content-Type" : "application/json"
            } , 
         
            credentials : "include"  , 
            withCredentials : true

            
        })
   
        console.log(resp.data);
        console.log(window.navigator.cookieEnabled);
  
        document.cookie = `verification=${resp.data.Token};Expires="Oct 5 2822 22:00:00 ;  path=/; SameSite=Lax`;
       await z(prev => ({...prev , Verification_route  : true , Verification : resp.data.message}))
       return navigate("/verification")

    } catch (error) {
 setLoainding(false)
        if(error.message === "Network Error"){

            return  setError((prev)=>{ return {class : "block" , data : "some issue please try again after somewhile"}})
        }
        return  setError((prev)=>{ return {class : "block" , data : error.response.data.message}})
    } finally {
        setLoainding(false)
        
    }
    
    
    
    // }

    
    
}





console.log(new Date());

return (
    <>
    <section className='registration'>
     <div className='registration_form end'>
        <form action="#" autoComplete='off' onSubmit={(e)=> handle_submit(e)} role="registration form">  
<div className='username flex'>
    


            <input   
             type="text"
             autoComplete='off'
             ref={useref}
             name="name"
             id="name"
             onChange={(e)=>{handle_username(e) ; setuer((prev)=>{return {...prev , username : e.target.value }});}}
             aria-describedby='username_ds'
             onFocus={()=> setClassNames((prev)=>{  return { ...prev , username :  'focused'}})}
             onBlur={(e)=> setClassNames((prev)=>{  return { ...prev , username : e.target.value == 0 ? ("") : ("focused color-inherit") }})}
             /> 

            <label htmlFor="name" className={`${classNames.username}`}>username</label>


    
            <div className='ok_icon'>
            <img className='ok_icon_img' src={okay_img.username}  />
            </div>
        </div>  
    <p  style={{position : "absolute" , top : "-342234px"}}  id='username_ds'>username must include atleast 4 character and maxmimum 50 character</p>


<div className='sirname flex'>
            <input   
             type="text"
             autoComplete='off'
             name="sir-name"
             id="sir-name"
             aria-aria-describedby='sirname_ds'
             onChange={(e)=>{ handle_sirname(e); setuer((prev)=>{return {...prev , sirname : e.target.value }});}}
             onFocus={()=> setClassNames((prev)=>{  return { ...prev , sirname :  'focused'}})}
             onBlur={(e)=> setClassNames((prev)=>{  return { ...prev , sirname : e.target.value == 0 ? ("") : ("focused color-inherit") }})}
             /> 


            <label htmlFor="sir-name"
               className={`${classNames.sirname}`}>
sirname
</label>
<div className='ok_icon'>
            <img className='ok_icon_img' src={okay_img.sirname} alt="" />
            </div>
<p  style={{position : "absolute" , top : "-342234px"}}  id='sirname_ds'>username must include atleast 4 character and maxmimum 50 character</p>

        </div>     
     
<div className='email flex'>
            <input   
             type="email"
             autoComplete='off'
             name="email"
             id="email"
             aria-describedby='email_ds'
             onChange={(e)=>{ handle_email(e); setuer((prev)=>{return {...prev , email : e.target.value }});}}
             onFocus={()=> setClassNames((prev)=>{  return { ...prev , email :  'focused'}})}
             onBlur={(e)=> setClassNames((prev)=>{  return { ...prev , email : e.target.value == 0 ? ("") : ("focused color-inherit") }})}
             /> 
            <label htmlFor="email"
               className={`${classNames.email}`}>
email
</label>
<div className='ok_icon'>
            <img className='ok_icon_img' src={okay_img.email} alt="" />
            </div>
<p  style={{position : "absolute" , top : "-342234px"}}  id='email_ds'>enter the email which you wanna to sign up with to our platform</p>

        </div>     
<div className='password flex'>
            <input   
             type="text"
             autoComplete='off'
             name="password"
             id="password"
             onChange={(e)=>{ handle_password(e); setuer((prev)=>{return {...prev , password : e.target.value }});}}
             onFocus={()=>{ setClassNames((prev)=>{  return { ...prev , password :  'focused'}}); setPwd_ds({ok_icon : "ok_icon_for_password" , ds : "pwd_ds"})}}
             onBlur={(e)=>{ setClassNames((prev)=>{  return { ...prev , password : e.target.value == 0 ? ("") : ("focused color-inherit") }}); setPwd_ds({ok_icon : null, ds : null})   }}
             /> 
            <label htmlFor="password"
               className={`${classNames.password}`}>
password
</label>
<p id="pwdnote"  className={`${pwd_ds.ds} password_ds`} >
                 <FontAwesomeIcon icon={faInfoCircle} />
                  8 to 24 characters.<br />
                 Must include uppercase and lowercase letters, a number and a special character.<br />
                Allowed special characters: <span aria-label="exclamation mark">!</span> <span aria-label="at symbol">@</span> <span aria-label="hashtag">#</span> <span aria-label="dollar sign">$</span> <span aria-label="percent">%</span>
            </p>

        </div>     
<div className={`ok_icon ${pwd_ds.ok_icon}`}>
            <img className={`ok_icon_img1 `}  src={okay_img.password} alt="" />
            </div>
<div className='username flex'>
            <input   
             type="text"
             autoComplete='off' 
             name="confirm-password"
             id="confirm-password"
             aria-describedby='confirm_pwdnote'
             onChange={(e)=>{ handle_confirmPassword(e); setuer((prev)=>{return {...prev , confirm_password : e.target.value }});}}
             onFocus={()=>{ setClassNames((prev)=>{  return { ...prev , confirm_password :  'focused'}}); set_confirm_pwd_ds("pwd_ds")}}
             onBlur={(e)=>{ setClassNames((prev)=>{  return { ...prev , confirm_password : e.target.value == 0 ? ("") : ("focused color-inherit") }}); set_confirm_pwd_ds(null)}}
             /> 
            <label htmlFor="confirm-password"
               className={`${classNames.confirm_password}`}>
confirm-password
</label>
<div className='ok_icon'>
            <img className='ok_icon_img' src={okay_img.confirm_password} alt="" />
            </div>
<p id="confirm_pwdnote"  className={`${confirm_pwd_ds} password_ds`} >
                  please re-write the password.
            </p>
        </div>     
<div className='username flex'>
            <input   
             type="date"
             autoComplete='off'
             aria-describedby='birth'
             name="birth"
             id="birth"
             onChange={(e)=>{ handle_birth(e); setuer((prev)=>{return {...prev , birth : e.target.value }}); }}
             onFocus={()=> setClassNames((prev)=>{  return { ...prev , birth :  'focused'}})}
             onBlur={(e)=> setClassNames((prev)=>{  return { ...prev , birth : e.target.value == 0 ? ("") : ("focused color-inherit") }})}
             /> 
            <label htmlFor="birth"
               className={`${classNames.birth}`}>
birth
</label>
<div className='ok_icon'>
            <img className='ok_icon_img' src={okay_img.birth} alt="" />
            </div>
 <p  style={{position: "absolute", bottom:"5536rem"}} id='birth'>birth field please give your birth date here.</p>

        </div>     

        </form> 
       <div class={"error"} style={{display : error.class }}>{error.data}</div>
        <footer className=''>
            <button >sign in</button>
            <button type='submit' className={`${sign_btn} sign-btn ${Loading ? ("loading") : ("")}`} onClick={(e)=> handle_submit(e)} >{Loading ? (<img src={loading} alt="" />) : ("sign in")}</button>
        </footer>
     </div>

   
    </section>  

    </>
)
}

export default Registration